      p Hacia el año 2013, la Organización Panamericana de la Salud y la Organización Mundial de la Salud, en el 52° Consejo Directivo y por medio de la Resolución CD52.R6, la cual abordó las causas de las disparidades en cuanto al acceso y la utilización de los servicios de salud por parte de las personas lesbianas, homosexuales, bisexuales y trans, resolvió pedir a los estados:

