      p En 2015, doce entidades de las Naciones Unidas expresaron su preocupación por el incremento de las víctimas de violencia contra los derechos humanos de las personas que componen los sectores LGBTIQ+ y orientaciones sexuales e identidades de género no hegemónicas y sus familias, creando exclusión social y económica, impactando el crecimiento económico, el trabajo decente y el progreso para alcanzar los Objetivos de Desarrollo Sostenible (ODS). Por lo cual, emitieron una #[span.txt-color-primario “Declaración Conjunta”] solicitando a los Estados actuar con premura para eliminar la violencia y la discriminación contra los adultos, adolescentes y niños de los sectores LGBTIQ+ En este mismo año, 193 Estados Miembros de las Naciones Unidas adoptaron por unanimidad los #[span.txt-color-primario Objetivos de Desarrollo Sostenible] hasta el año 2030 y el #[span.txt-color-primario “Compromiso de no dejar a nadie atrás”].

