    p.mb-5(data-aos='fade-right') A continuación, se presenta un resumen con otras normas internacionales de derechos humanos frente a la eliminación de la violencia y discriminación hacia las personas que componen los sectores LGBTIQ+, y orientaciones sexuales e identidades de género no hegemónicas, las cuales puede consultar para ampliar la información al respecto: 


    .bg-info.mn.pb-0.pt-0
      ImagenInfografica.color-acento-botones
          template(v-slot:imagen)
            figure
              img(src='@/assets/curso/infografia_1.svg', alt='Texto que describa la imagen')
          .tarjeta.bg-white.shadow.p-3(x="42%" y="26%" numero="+")
            .h5.mb-2 Declaración de Montreal sobre los Derechos Humanos de Lesbianas, Gay, Bisexuales y Transexuales
            p En 2006 en Montreal, Quebec, Canadá, la Conferencia Internacional sobre los Derechos Humanos LGBT establecen una serie de derechos universales que se deben garantizar así, como sus libertades, relativos a las personas que componen los sectores LGBTIQ+. logrados a partir de las demandas y exigencias. 
          .tarjeta.bg-white.shadow.p-3(x="39%" y="36.5%" numero="+")
            .h5.mb-2 Principios de Yogyakarta
            p En 2007, se establecen 29 Principios sobre la aplicación de la legislación internacional de derechos humanos en relación con la orientación sexual e identidad de género.  Estos Principios de Yogyakarta corresponden con aquellos relativos a la orientación sexual e identidad de género, encaminada a la interpretación y aplicación de las normas del Derecho internacional de los derechos humanos de las personas que componen los sectores LGBTIQ+. En 2017, se adoptaron los principios de Yogyakarta «plus 10» como un suplemento a los ya contemplados en 2007.
            
          .tarjeta.bg-white.shadow.p-3(x="39%" y="46.5%" numero="+")
            .h5.mb-2 Declaración sobre orientación sexual e identidad de género de las Naciones Unidas (2008)
            p Esta Declaración condena la violencia, el acoso, la discriminación, la exclusión, la estigmatización y el prejuicio basado en la orientación sexual y la identidad de género, así mismo los asesinatos, ejecuciones, torturas, arrestos arbitrarios y la privación de derechos económicos, sociales y culturales por estos motivos.
            
          .tarjeta.bg-white.shadow.p-3(x="32.5%" y="57%" numero="+")
            .h5.mb-2 Resolución 2435 (XXXVIII- O/08) DE LA ASAMBLEA GENERAL DE LA OEA: 
            p En 2008 la Asamblea General de la OEA establece la Declaración Universal de los Derechos Humanos frente a la libertad y los derechos y libertades de los seres humanos sin distinción de cualquier naturaleza tales como raza, color, sexo, idioma, religión, opinión política o de cualquier otra índole, origen nacional o social, posición económica, nacimiento o cualquier otra condición.
          .tarjeta.bg-white.shadow.p-3(x="31%" y="67%" numero="+")
            .h5.mb-2 Resolución 2504 (XXXIX- O/09) DE LA ASAMBLEA GENERAL DE LA OEA
            p En 2009, se condena los actos de violencia y las violaciones de derechos humanos relacionadas, perpetrados contra individuos a causa de su orientación sexual e identidad de género.  En esta Asamblea se solicita a los Estados a asegurar que se investiguen y se juzguen los actos de violencia y las violaciones de derechos humanos realizados en estos contextos.

          .tarjeta.bg-white.shadow.p-3(x="60%" y="26%" numero="+")
            .h5.mb-2 Resolución 2600 (XL-O/10) DE LA ASAMBLEA GENERAL DE LA OEA 
            p En 2010 se condena la impunidad a los actos de violencia. De igual manera, se insta a los Estados Miembros a establecer mecanismos para combatir la discriminación contra personas a causa de su orientación sexual e identidad de género.
            
          .tarjeta.bg-white.shadow.p-3(x="61%" y="36.5%" numero="+")
            .h5.mb-2 Resolución 2653 (XLI-O/11) DE LA ASAMBLEA GENERAL DE LA OEA
            p En 2011 se solicita a los Estados adoptar las medidas necesarias para prevenir, sancionar y erradicar la discriminación e investigar dichos actos y asegurar a las víctimas discriminadas por su orientación sexual, la protección judicial en condiciones de igualdad y que los responsables enfrenten las condenas definidas por la justicia propias de cada Estado.
            
          .tarjeta.bg-white.shadow.p-3(x="58%" y="46.5%" numero="+")
            .h5.mb-2 Resolución 17/19, Derechos humanos, orientación sexual e identidad de género, A/HRC/RES/17/19, de 14 de julio de 2011 del Consejo de Derechos Humanos
            p Primera Resolución de las Naciones Unidas, precisa Leyes, prácticas discriminatorias y actos de violencia contra las personas de los sectores LGBTI, de igual manera, pide a los Estados que revoquen las penalizaciones a las relaciones consensuadas entre adultos del mismo sexo y que reconozcan legalmente el género con el que las personas trans se identifican.
            
          .tarjeta.bg-white.shadow.p-3(x="67%" y="57%" numero="+")
            .h5.mb-2 Observación General No. 18, No discriminación, 37º período de sesiones, 11 de octubre de 1989, HRI/GEN/1/Rev.7.ONU. Comité de los Derechos Humanos.
            p Referencia el término de «discriminación» y cómo se caracteriza en dichos contextos.
            
          .tarjeta.bg-white.shadow.p-3(x="69.5%" y="66.5%" numero="+")
            .h5.mb-2 ONU. Observación General No. 20 de 2009 (E/C. 12/GC/20).
            p Referencia la no discriminación y los derechos económicos, sociales y culturales
            

    .bg-green.mn.p-2
      p.mb-0.p-0.text-center Nota. Lineamiento para la atención en salud de las personas trans y no binarias: aproximaciones iniciales. SDS.2021.

    .bg-tema-2.mn.mb-5
      p Sumado a esto, la #[span.color-primario Asamblea General de las Naciones Unidas y el Consejo de Derechos Humanos], preocupados por la continua impunidad sobre las violaciones de los derechos humanos de las personas que, por su orientación sexual o identidad de género, han sido víctimas, establecieron una serie de resoluciones que han sido aprobadas bajo mandato, amparando la inclusión y garantía del respeto por los derechos humanos, aprobando las siguientes:
      .tarjeta.bg-white.p-3.p-lg-5
