                h4(style="color:#0F5FFE ")  A/HRC/RES/17/19 - Adoptada 17 de junio 2011
                p Documentar los actos de discriminación y violencia contra personas por su orientación sexual e identidad de género en todo el mundo. Evaluar cómo la normativa internacional de derechos humanos puede ser adoptada para poner fin a las acciones violentas originadas por la orientación sexual y la identidad de género.
              .col-auto: img(src='@/assets/curso/40.svg', alt='')
            .row(titulo="2014")
              .col.mb-4.mb-lg-0
                h4(style="color:#0F5FFE ") A/HRC/RES/27/32 - Adoptada el 26 de septiembre de 2014
                p Se solicita al Alto Comisionado actualizar el informe (A/HRC/19/41) con el fin de compartir buenas prácticas y mecanismos para superar la violencia y discriminación contra personas por su orientación sexual e identidad de género, en conformidad con las normas y el derecho internacional.
              .col-auto: img(src='@/assets/curso/40.svg', alt='')
            .row(titulo="2016")
              .col.mb-4.mb-lg-0
                h4(style="color:#0F5FFE ") A/HRC/RES/32/2 - Adoptada 30 de junio de 2016
                p Reafirmar que todos los seres humanos nacen libres e iguales en dignidad y derechos sin excepción alguna por raza, color, sexo, idioma, religión, opinión política o de cualquier otra índole, origen nacional o social, posición económica, nacimiento o cualquier otra condición.
              .col-auto: img(src='@/assets/curso/40.svg', alt='')
            .row(titulo="2019")
              .col.mb-4.mb-lg-0
                h4(style="color:#0F5FFE ")  A/HRC/RES/41/18 - 19 de julio de 2019
                p Protección contra la violencia y discriminación por motivos de orientación sexual e identidad de género.
              .col-auto: img(src='@/assets/curso/40.svg', alt='')
    p.mb-4(data-aos='fade-right') Para integrar los conceptos recién adquiridos, lo invitamos a sumergirse en la siguiente actividad de aprendizaje: 
        
