    #t_1_2.titulo-segundo.color-acento-contenido(data-aos='fade-right')
      h2 1.2 Normativa nacional: atención a población LGBTIQ+
    p(data-aos='fade-right') Tal como lo decía Julieta Lemaitre Ripoll, en su artículo en la Revista Internacional de Derechos Humanos-Sur (2009):
    
    .bg-tema-3.mn
      .row.justify-content-center.align-items-center
        .col-lg-8.mb-3(data-aos="fade-right")
          .tarjeta(style="background-color: #DEE4F0").tarjeta-cita.p-5
              .tarjeta-cita__comilla1: img(src='@/assets/curso/comilla1.svg', alt='')
              .tarjeta-cita__comilla2: img(src='@/assets/curso/comilla2.svg', alt='') 
              p.fst-italic.mt-3 Es  muy probable que la razón principal por la cual el derecho es un marco de referencia tan importante para las organizaciones LGBT sea la jurisprudencia. La Constitución de 1991, y en especial la interpretación que le dio la Corte Constitucional, han sido centrales para movilizar liderazgos, brindar un vocabulario y un escenario para hacer exigencias. Si bien la Constitución misma no menciona los derechos de los homosexuales, la Corte, en una serie de sentencias liberales, extendió los derechos a la igualdad y a la dignidad humana para incluir la protección contra la discriminación por orientación sexual. Con estas decisiones le dio un nuevo marco de significado a la orientación sexual de manera que ésta ha pasado de ser una cuestión de cultura y de “estilo de vida” a ser un problema de derechos.
                br
                br
                |La jurisprudencia sobre orientación sexual fue iniciada a mediados de la década del noventa por las demandas de tutela de individuos que, en general sin relación entre ellos, enmarcaban su sufrimiento como violaciones de derechos. De esta época se destaca el abogado Germán Rincón Perfetti, que empezó a utilizar de manera más o menos sistemática la acción de tutela para proteger los derechos de individuos discriminados por ser homosexuales.(p.81).
        .col-lg-4.col-7(data-aos="fade-left"): img(src='@/assets/curso/15c.png', alt='')


    p.mb-5.pb-3 En este contexto histórico, presentamos el resumen del marco normativo colombiano que da cuenta a la exigibilidad de los derechos humanos y la garantía del ejercicio efectivo de los derechos de las personas que componen los sectores LGBTIQ+ y las orientaciones sexuales e identidades de género no hegemónicas: 

    .mn.mb-5(style="background-color: #423755 ").py-0
      .row.justify-content-center.align-items-center
        .col-lg-5.col-7.mb-lg-0.mb-3: img(src='@/assets/curso/41e.svg', alt='' style="margin-top: -2rem")
        .col-lg-7(data-aos="fade-right")
          p.mb-0.text-white.p-4 #[strong La Constitución Política de Colombia], promulgada en 1991, es un documento clave que define la estructura y los principios del Estado colombiano. No solo establece la organización política y administrativa del país, sino que también consagra los derechos y deberes de los ciudadanos y las instituciones. La Constitución tiene como objetivos fundamentales garantizar la convivencia pacífica, respetar la dignidad humana y reforzar un Estado social de derecho, comprometiéndose a fortalecer la unidad nacional y asegurar a sus integrantes derechos fundamentales como la vida, la justicia, la igualdad, y la libertad. Entre ellos están:  
    .row.justify-content-center.mb-5
      .col-lg-8.position-relative 
        .avatar
          figure
            img(src="@/assets/curso/42.svg", alt="alt").img250
        .crd.crd--avatarHorizontal.crd--first.py-4
          .row.align-items-center
            .col-auto
              figure
                img(src="@/assets/curso/i1.svg", alt="alt").img100
            .col
              p #[strong Artículo 1.] Colombia es un Estado social de derecho, (...) organizado en forma de República unitaria democrática, participativa y pluralista, fundada en el respeto de la dignidad humana. 
        .crd.crd--avatarHorizontal.py-4
          .row.align-items-center
            .col-auto
              figure
                img(src="@/assets/curso/i2.svg", alt="alt").img100
            .col
              p #[strong Artículo 2.] Son fines esenciales del Estado:(...) garantizar la efectividad de los principios, derechos y deberes consagrados en la Constitución.
        .crd.crd--avatarHorizontal.py-4
          .row.align-items-center
            .col-auto
              figure
                img(src="@/assets/curso/i1.svg", alt="alt").img100
            .col
              p #[strong Artículo 5.] El Estado reconoce, sin discriminación alguna, la primacía de los derechos inalienables de la persona...”
        .crd.crd--avatarHorizontal.crd--last.py-4
          .row.align-items-center
            .col-auto
              figure
                img(src="@/assets/curso/i2.svg", alt="alt").img100
            .col
              p #[strong Artículo 13.] Todas las personas nacen libres e iguales ante la ley, recibirán la misma protección y trato de las autoridades y gozarán de los mismos derechos, libertades y oportunidades sin ninguna discriminación por razones de sexo, raza, origen nacional o familiar, lengua, religión, opinión política o filosófica”. “El Estado promoverá las condiciones para que la igualdad sea real y efectiva y adoptará medidas en favor de grupos discriminados o marginados”
        


    //todo! Infografia pendiente 
    p.mb-5(data-aos='fade-right') En este contexto histórico, se presenta el marco normativo colombiano que da cuenta a la exigibilidad de los derechos humanos y la garantía del ejercicio efectivo de los derechos de las personas que componen los sectores LGBTIQ+ y las orientaciones sexuales e identidades de género no hegemónicas:

    .tarjeta-slyder.tarjeta.p-5.mb-5
      SlyderA(tipo="b")
        .row.align-items-start
          .col-lg-7.mb-4.mb-lg-0
            h4(style="color: #0F5FFE").mb-4 Ley 1257 de 2008
            p Esta Ley establece que la sociedad y la Familia son responsables de respetar los derechos de las mujeres y de contribuir a la eliminación de la violencia contra ellas. El Estado es responsable de prevenir, investigar y sancionar toda forma de violencia contra las mujeres. De igual manera, reforma los Códigos Penal, de Procedimiento Penal, la ley 294 de 1996.
          .col-lg-5
            figure
              img(src='@/assets/curso/43.png', style="max-width:420px").mx-auto
        .row
          .col-lg-7.mb-4.mb-lg-0
            h5(style="color: #0F5FFE").mb-4 Ley Antidiscriminación 1482 de 2011
            p Esta ley tiene por objeto garantizar la protección de los derechos de una persona, grupo de personas, comunidad o pueblo, que son vulnerados a través de actos de racismo o discriminación. Al igual define la “discriminación arbitraria como toda distinción, exclusión o restricción que carezca de justificación razonable, efectuada por agentes del Estado o particulares, y que cause privación, perturbación o amenaza en el ejercicio legítimo de los derechos fundamentales establecidos en la Constitución Política”.
          .col-lg-5
            figure
              img(src='@/assets/curso/44b.png', style="max-width:420px").mx-auto
        .row
          .col-lg-7.mb-4.mb-lg-0
            h5(style="color: #0F5FFE").mb-4 Ley 1448 de 2011
            p ARTÍCULO 13. ENFOQUE DIFERENCIAL. reconoce que hay poblaciones con características particulares debido a su género, orientación sexual y situación de discapacidad. Por tal razón, se establecen las medidas de ayuda humanitaria, atención, asistencia y reparación integral.
          .col-lg-5
            figure
              img(src='@/assets/curso/45.png', style="max-width:420px").mx-auto

        .row
          .col-lg-7.mb-4.mb-lg-0
            h5(style="color: #0F5FFE") Ley 1438 de 2011
            p Por medio de la cual se reforma el Sistema General de Seguridad Social en Salud y se dictan otras disposiciones.
          .col-lg-5
            figure
              img(src='@/assets/curso/46.png', style="max-width:420px").mx-auto
        .row
          .col-lg-7.mb-4.mb-lg-0
            h5(style="color: #0F5FFE") Resolución 459 de 2012
            p “Por lo cual se adopta el Protocolo de Atención Integral en Salud para Víctimas de Violencia Sexual”.
          .col-lg-5
            figure
              img(src='@/assets/curso/47.png', style="max-width:420px").mx-auto
        .row
          .col-lg-7.mb-4.mb-lg-0
            h5(style="color: #0F5FFE") Resolución 0163 de 2013
            p Establece Lineamientos Técnicos de Violencias Basadas en Género para las Comisarías de Familia.
          .col-lg-5
            figure
              img(src='@/assets/curso/48.png', style="max-width:420px").mx-auto
        .row
          .col-lg-7.mb-4.mb-lg-0
            h5(style="color: #0F5FFE") Ley 1616 de 2013
            p Esta Ley, precisa la atención integral en salud mental por talento humano, con recursos suficientes y pertinentes en salud para responder a las necesidades de salud mental de la población, incluyendo entre las acciones a desarrollar la promoción del buen trato y la prevención de las violencias como acciones transversales para mejora de la salud mental de la población.
          .col-lg-5
            figure
              img(src='@/assets/curso/49.png', style="max-width:420px").mx-auto

        .row
          .col-lg-7.mb-4.mb-lg-0
            h5(style="color: #0F5FFE") Política Nacional de Sexualidad, Derechos Sexuales y Derechos Reproductivos (2014)
            p Instauró el derecho al disfrute de la sexualidad asociado con el desarrollo de autonomía de lo(a)s personas.
          .col-lg-5
            figure
              img(src='@/assets/curso/50.png', style="max-width:420px").mx-auto

        .row
          .col-lg-7.mb-4.mb-lg-0
            h5(style="color: #0F5FFE") Decreto 1066 de 2015
            p Decreto Único Reglamentario del Sector Administrativo del Interior. En el marco de la revisión, se cita:
            p #[strong Igualdad y no discriminación:] donde se garantice el respeto y el derecho a la Igualdad; protección y trato a los familiares de las víctimas, sin distinción de etnia, identidad de género, orientación sexual, cultura, entre otras.
            p #[strong Enfoque diferencial:] Las autoridades adoptaran las medidas que reconozcan las particularidades poblacionales, principalmente de los sujetos de especial protección constitucional, es decir, aquellos que, por sus características de género, orientación sexual, se encuentren en condición de vulnerabilidad y que requieren una atención y protección diferenciada y la implementación de políticas de acción afirmativa, acordes con su situación.
          .col-lg-5
            figure
              img(src='@/assets/curso/51.png', style="max-width:420px").mx-auto



    p.mb-5(data-aos='fade-right') Otras normas que existen en Colombia para la exigibilidad y garantía del ejercicio efectivo de los derechos de las personas que componen los sectores LGBTIQ+ y las orientaciones sexuales e identidades de género no hegemónicas:
    .row.justify-content-center.mb-5
      .col-lg-5.col-7(data-aos="fade-right"): img(src='@/assets/curso/52b.png', alt='') 
      .col-lg-7(data-aos="fade-left")
        AcordionA.mb-5(tipo="a" clase-tarjeta="tarjeta rounded-1 tarjeta--azul")
          .p-4(titulo="Resolución 518 de 2015")
            p Gestión de la Salud Pública y la Ejecución del Plan de Salud Pública de Intervenciones Colectivas – PIC.

          .p-4(titulo="Ley Estatutaria 1751 de 2015")
            p Declara la salud como un derecho humano fundamental, formulando la Política de Atención Integral en salud (PAIS) y al Modelo de Atención Integral Territorial (MAITE), anteriormente Modelo Integral de Atención en Salud (MIAS) con la configuración de la Ruta de Promoción y Mantenimiento de la Salud (Resolución 3280 de 2018).

          .p-4(titulo="Decreto 762 de 2018")
            p Esta política pública tiene como objetivo garantizar el ejercicio pleno de derechos a las personas que componen los sectores LGBTIQ+como parte de la producción, gestión social y bienestar colectivo de la ciudad.
          .p-4(titulo="Plan Decenal de Salud Pública 2022-2031")
            p Incorpora las categorías de la diversidad que orientarán el abordaje de los sujetos de derecho y la cualificación de sus acciones.     
          
          .p-4(titulo="Resolución 295 de 2023")
            p Modifican la gestión de la salud pública, las responsabilidades de las entidades territoriales y de los ejecutores del Plan de Salud Pública de Intervenciones Colectivas, así como las condiciones para la ejecución.

          .p-4(titulo="Resolución 2138 de 2023")
            p Por la cual se modifican los artículos 1, 2 y 3 de la Resolución 1035 de 2022 y los capítulos 3, 4, 5, 6, 8, 9, 10 y 11 del anexo técnico "Plan Decenal de Salud Pública 2022-2031". 

          .p-4(titulo="Resolución 2367 de 2023")
            p Por la cual se adoptan los lineamientos de transversalización del enfoque de género en el sector salud para el cierre de brechas por razones de sexo, género, identidad de género y orientación sexual.
          

    .mn.mb-5(style="background-color: #3c1c74 ").py-0
      .row.justify-content-center.align-items-center
        .col-lg-7.mb-lg-0.mb-3(data-aos="fade-right")
          p.mb-0.text-white.p-4 Acorde con las directrices internacionales, Colombia adopta los lineamientos establecidos en la Declaración Universal de los Derechos Humanos; por medio de leyes y políticas que garanticen el ejercicio efectivo de los derechos. En este sentido, y en el contexto de este curso, iniciaremos con la Ley Estatutaria 1751 de 2015, la cual tiene por objeto Garantizar el Derecho Fundamental de la Salud tanto en lo individual como en lo colectivo, con características básicas de autonomía e irrenunciabilidad. Esto incluye a las personas que componen los sectores LGBTIQ+ y orientaciones sexuales e identidades de género no hegemónicas, quienes deben contar con acceso a los servicios de salud con oportunidad, eficacia y calidad, teniendo como ejes indispensables el preservar y mejorar la salud. En las múltiples acciones en salud, en lo que corresponde con la promoción, prevención, diagnóstico, tratamiento, rehabilitación y cuidados paliativos, el Estado debe garantizar la igualdad de acceso y calidad en la prestación del servicio de salud.      
        .col-lg-5.col-7: img(src='@/assets/curso/54c.svg', alt='' style="margin-top: 0rem")
