          p.mb-0.p-4 Dentro de este proceso de promoción de la salud encontramos la Política Nacional de Derechos Sexuales y Derechos Reproductivos - PNSDSDR. Esta política, actualizada en el marco del Plan Decenal de Salud Pública, 2022 a 2031 por medio de la Resolución 2367 de 2023, reconoce a       la sexualidad como una condición esencialmente humana que está presente en todos los momentos del curso de vida, lo que permite abordarla desde lo social y no solo desde lo biológico; este abordaje social, implica, claramente, todos sus determinantes sociales de la salud, el bienestar de las personas y las condiciones de vida, dentro de un marco de derechos. Se debe tener cuenta que acorde con las estadísticas, las personas que componen los sectores LGBTIQ+ y con orientaciones sexuales e identidades de género no hegemónicas, son población altamente vulnerable, por esta razón el actual Plan Decenal “adoptó el enfoque diferencial como un componente trasversal y estratégico que permitiera la aplicación de medidas especiales en favor de aquellos grupos sociales en los que dichas diferencias significan desventajas” (Res /2367 de 2023, Pag 9). Por otro lado, se suma el dejar de asociar la sexualidad a las patologías que la afectan, posición que ha restringido logros sustanciales, para que la población colombiana, independientemente de su edad, sexo, identidad de género, orientación sexual, pertenencia étnica, cultura, situación económica, nivel educativo; la viva plenamente dentro de un marco de derechos.


