      .tarjeta(style="background-color:#423755").p-4
        .row
          .col-auto
            figure
              img(src='@/assets/curso/57c.svg', alt='', style="max-width: 63px").mx-auto
          .col 
            p.mb-0.text-white Es por eso que el personal médico ha requerido capacitación en materia de derechos humanos, derechos de las personas que componen los sectores LGBTIQ+ y orientaciones sexuales e identidades de género no hegemónicas, y derechos sexuales y reproductivos, fomentando así reducir la discriminación y eliminar las barreras para la garantía del derecho a la salud para esta población.



    p.mb-5(data-aos='fade-right') En el marco de la Política Nacional de Derechos Sexuales y Derechos Reproductivos - PNSDSDR, es necesario dar a conocer el concepto del goce de la vida sexual. Respecto a este concepto, el Ministerio de Salud y Protección Social. manifiesta que:

    .mn.mb-5(style="background-color: #e1fbf3 ").py-0
      .row.justify-content-center.align-items-center
        .col-lg-5.col-7.mb-lg-0.mb-3: img(src='@/assets/curso/58b.svg', alt='' style="margin-top: -2rem")
        .col-lg-7(data-aos="fade-right")
          .p-3
            h5 Derecho al goce, la satisfacción y la gratificación sexual
            p.mb-0 Este derecho parte del reconocimiento que tiene toda persona a sentir placer, a disfrutar de su sexualidad, a autosatisfacerse y experimentar con los sentidos, implica que todas las personas tienen el derecho a vivir su sexualidad sin vincularla a la reproducción.
    p.mb-5(data-aos='fade-right') La #[span.color-primario Política de Atención Integral en Salud] (PAIS) Resolución 429 de 2016,  centra el enfoque del sistema en la persona, a través de #[span.color-primario la regulación de la intervención de los responsables de garantizar la atención de la salud en condiciones de accesibilidad, aceptabilidad, oportunidad, continuidad e integralidad]. El enfoque diferencial es la estrategia que permite a la política de atención reconocer y organizarse frente a las diferencias de las personas y colectivos, como el caso de las personas que componen a los sectores LGBTIQ+ identificando condiciones de vulnerabilidad asociadas a factores físicos, psíquicos, sociales, psicosociales, culturales (creencias y valores), económicos, ambientales, entre otros.
    
